.filter-bar {
    position: fixed;
    top: 0;
    left: 0;
    width: 200px; /* Adjust the width as needed */
    height: 100vh; /* Set the desired height */
    background-color: #f0f0f0;
    padding: 20px;
  }
  
  .filter {
    margin-bottom: 10px;
    padding: 8px;
    background-color: #d0d0d0;
    color: #333;
    font-weight: bold;
    cursor: pointer;
  }
  
  .filter:hover {
    background-color: #a0a0a0;
    color: #fff;
  }
  